import { Component, OnInit, Input } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AlertController, LoadingController, NavController, ToastController, Platform } from '@ionic/angular';
import { AccessProviders } from '../providers/access-providers';
import { Storage } from '@ionic/storage';
import { NavparamService } from '../navparam.service';
import { DataService } from '../data.service';


@Component({
  selector: 'app-productlist',
  templateUrl: './productlist.page.html',
  styleUrls: ['./productlist.page.scss'],

})
export class ProductlistPage implements OnInit {

  prods: any =[];
    
  device = true; 
  web = true;

  constructor(
    private router: Router,
    private dataService: DataService,
    private loadingController: LoadingController,
    private toastController: ToastController,
    private alertController: AlertController,
    private accessProviders: AccessProviders,
    public navController: NavController,
    private storage: Storage,
    private platform: Platform,
    private activatedRoute: ActivatedRoute,
    private navParamService: NavparamService
    ) { 
      this.device = this.platform.is('mobile');
      this.web =this.platform.is('desktop');
    }

    ngOnInit() {
   
      return new Promise(resolve =>{
        let body = {
          aksi: 'get_products', 
          
        }   
        this.accessProviders.postData(body, 'get_products.php').subscribe((res:any) => {
          
          if(res.success == true) {
             console.log(res.msg);
             console.log(res.result);
             for(let datas of res.result){
               this.prods.push(datas);
             }
             console.log(this.prods);
          } else {
            this.presentToast('Failed'); 
          }
        });   
        
      });
        
    }

    viewProduct(product_id){
      console.log(product_id); 
      this.navParamService.setNavData(product_id);
      this.router.navigate(['/product-detail']);
    }
    
  async presentToast(a) {
    const toast = await this.toastController.create({
      message: a,
      duration: 1500,
      position: 'bottom'
    });
    toast.present();
  }  
}
